@import url('https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap');

html {
    line-height: 1.15;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family:'RocknRoll One', sans-serif;
}
body{
    background: linear-gradient(
        190deg, #867bcd 0%,#6a5aab 100%
    );
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: #222;
    overflow: hidden;
}
.App{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
}
.App h1{
    display: inline;
    text-align: center;
    margin-bottom: 2rem;
    color: #e1ebfd;
    text-shadow: 0 0 5px #433aa8,
    3px -1px 5px #271c6c;
}
.addTodos {
    display: flex;
    justify-content: center;
}
.todo-input{
    min-width: 15rem;
    width: 40vw;

    max-height: 2.5rem;
    background-color: #e1ebfd;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    align-self: center;
}
.todo-input:focus{
    outline: none;
    border: 2px solid #433aa8;
}
.add-btn{
    margin-left: 1rem;
    background-color: #271c6c;
    color: #e1ebfd;
    border-radius: 50%;
    border: 2px solid #e1ebfd;
    font-size: 1.5rem;
    width: 3.2rem;
    height: 3.2rem;
    cursor: pointer;
    box-shadow: 2px 4px 10px #271c6c;

    display: flex;
    justify-content: center;
    align-items: center;
}
.add-btn:focus{
    outline: none;
}
